* {
  margin: 0;
  padding: 0;
}
.copied_warehouse_top {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4rem;
  padding: 1rem;
  margin: 1rem;
}

.copied_warehouse_top_btn {
  padding: 0.3rem;
  width: 100%;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: grid;
  grid-template-columns: 0.3fr 1.5fr;
  box-shadow: 0 0rem 0.5rem rgba(0, 0, 0, 0.3);
  background-color: white;
}

.copied_warehouse_icon {
  padding: 0.6rem;
  color: white;
  margin-right: 0.6rem;
  border-radius: 0.3rem;
  width: 1rem;
}
.copied_warehouse_text {
  padding: 0.6rem 0rem;
  // font-size: 1rem;
  text-align: left;
}
.bg_rangi1 {
  background-color: skyblue;
}

.bg_rangi2 {
  background-color: rgb(250, 113, 89);
}

.bg_rangi3 {
  background-color: rgb(167, 48, 167);
}

.bg_rangi4 {
  background-color: rgb(47, 165, 86);
}

.copied_warehouse_bottom_card {
  border-radius: 1rem;
  padding: 2rem 2rem;
  margin: 2rem;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.3);
  /* height: 35rem; */
}

.warehouse_bottom_card_inside {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  // font-size: 1.5rem;
}

.copied_warehouse_card_title {
  display: grid;
  grid-template-columns: 0.1rem max-content 1rem;
  grid-template-rows: 1fr;
}

.copied_warehouse_card_title1 {
  color: orange;
  padding-left: 0.5rem;
  margin: 0;
}

.copied_vertical_line {
  width: 3px;
  /* height: 1.5rem; */
  background-color: green;
}

.copied_warehouse_card_addition_info {
  margin-left: 1.5rem;
  padding: 0rem 3rem;
  border: 1px solid rgb(206, 200, 200);
  /* height: 1.5rem; */
  display: grid;
  justify-content: center;
  border-radius: 0.3rem;
  // font-size: 18px;
  align-items: center;
}

.products_in_warehouse_line {
  background-color: grey;
  width: 100%;
  height: 1px;
  margin: 1.5rem 0rem;
}

.products_in_warehouse_title {
  // font-size: 2.2rem;
}

table {
  margin-top: 1rem;
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

.products_in_warehouse_table_head {
  background-color: rgba(108, 250, 108, 0.26);
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  text-align: center;
}

.products_in_warehouse_table_data {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  text-align: center;
}

tr:nth-child(even) {
  background-color: #fac3ff2d;
}

.td_nomer {
  font-weight: bold;
}

.products_in_warehouse_table {
  // font-size: 1.5rem;
}

.product_price_in_table {
  text-align: center;
}

.products_in_warehouse_trow {
  height: 5rem;
}
